import React from 'react'
import styled from 'styled-components';

const ValueSelector = styled.div`
    background-color: #ffffff;
    align-items: center;
    display: flex;
    width: 100%;
`;
const ValueInput = styled.input`
    font-size: 16px;
    border: none;
    width: 88%;
    padding: 0.5em;
    padding-left: 0.75em;

`;
const CurrencySymbol = styled.div`
    background-color: rgba(0, 0, 0, 0.05);
    font-size 16px;
    padding: 0.5em;
    font-weight: 900;
    width: 12%;

`;
const CurrencySelecter = styled.select`
    font-size: 16px;
    padding: 0.5em;
    border: none;
    appearance: button;
    width:100%;
    :hover {
        color: orange;
    }
    margin-bottom: 1em;
`;
const Selector = styled.div`
    font-size: 16px;
    padding: ;
    border-radius: ;
    width: 100%

`;

const CurrencySelector = ( { id, value, currency, setCurrency, setTargetValue, currencyData, currencyOrder} ) => {
    

    return (
       
        <>
        {console.log("CurrencySelector", id, ">> Render")}
        <Selector key ={id+"C"}>
            
            <CurrencySelecter default={currency} key ={id+"CA"} value={currency} onChange={e => setCurrency(e.target.value)} name="currency" id="currency">
                {console.log("CurrencySelecter", id, ">> Render")}
                {
       //             Object.keys(currencyData).map(((optionCurrency, index) => {
                        currencyOrder.map(((optionCurrency, index) => {
       //                 console.log(optionCurrency);
                        return (
                            <option key={id+"CA"+index}value={optionCurrency[0]}>{currencyData[optionCurrency[0]] ? `${currencyData[optionCurrency[0]]["currencyName"]}  (${optionCurrency[0]})` : "NONC"}</option>);
                    }))
                } 

            </CurrencySelecter>
            <ValueSelector key ={id+"D"}>
                {console.log("ValueSelector", id, ">> Render")}
                <ValueInput key ={id+"DA"} value = {value} onChange = {e => {const newValue = e.target.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1'); setTargetValue(newValue)}} type="text" name="textinputter"/>
                <CurrencySymbol key ={id+"DB"} >
                    {currencyData[currency] ? currencyData[currency]["currencySymbol"] : "NONC"}
                </CurrencySymbol>
            </ValueSelector>

            
        </Selector> 
        </>
    )

};

    export default CurrencySelector